import React from 'react';
import { Image, Container, Col, Row, Button } from 'react-bootstrap';
import bg_1 from '../images/sewer_concept.png';

import discord from '../images/navbar_icons/discord-line.png';
import os from '../images/navbar_icons/os-line.png';

import Header from './Header';
import './Bg.css';

const Bg = () => {
    return (
        <>
            <div className="cont">
                <Image variant="top" style={{ width: "100vw", borderRadius: "5px", minHeight: "100%", marginBottom: "12vh", filter: "blur(5px)", WebkitFilter: "blur(5px)" }} src={bg_1} />
                <div class="centered">
                    <Container>
                        <Row>
                            <Col xs={5} md={3}>
                            </Col>
                            <Col xs={8} md={6}>
                                <Header heading="MetaGunz"
                                    content={["MetaGunz is an NFT collection sold on the Polygon Blockchain and consists of 8,888 unique combinations of Gunz. Every gun is digitally drawn and assembled by code, no two Gunz are the same."]}
                                />

                                <Button
                                    href="https://discord.gg/BQApfxqAcW"
                                    className="disc-btn"
                                >
                                    <img
                                        src={discord}
                                        width="20"
                                        height="20"
                                        className="d-inline-block align-center"
                                        alt="Discord Link"
                                    />
                                    <strong> Discord</strong>
                                </Button>

                                <Button
                                    href="https://opensea.io/collection/metagunz-official"
                                    className="os-btn"
                                >
                                    <img
                                        src={os}
                                        width="20"
                                        height="20"
                                        className="d-inline-block align-center"
                                        alt="OpenSea Link"
                                    />
                                    <strong> OpenSea</strong>
                                </Button>
                            </Col>
                            <Col xs={5} md={3}>
                            </Col>
                        </Row>
                    </Container>
                </div>
            </div>

        </>
    )
}

export default Bg;